const StyledGrid = withStyles(() =>
  createStyles({
    root: {
      flexWrap: "nowrap",
      overflowY: "scroll",
      
      "&::-webkit-scrollbar": {
        width: "1px",
        height: "10px",
        paddingLeft:"20px",
      },
      "&::-webkit-scrollbar-track": {
        boxShadow: "inset 0 0 2px rgba(0,0,0,0.00)",
        webkitBoxShadow: "inset 0 0 2px rgba(0,0,0,0.00)",
      
      },
      "&::-webkit-scrollbar-thumb": {
        backgroundColor: "#787878",
        borderRadius: 2,
      },
    },
  })
)(Grid);



const StyledSelectBox = withStyles(() =>
  createStyles({
    root: {
      minheight: "7px",width:"25px",background:"transparent linear-gradient(180deg, #FCFCFC 0%, #F2F2F2 100%) 0% 0% no-repeat padding-box",boxShadow: "0px 1px 1px #0000001A",
                          border: "1px solid #D2D2D2",
      opacity: 1,
                          padding:"5 5 5 5px"
    },
  })
)(MuiSelect);

function SelectLocations(props) {
  const [formvalues, setFormvalues] = useState(null);
  const [key, setKey] = useState(false);
  const [addressData, setAddressData] = useState([]);

  const [truckList, setTruckList] = React.useState([0]);
  const [multiLocFlag, SetMultiLocFlag] = useState(false);
  const [multiTimeFlag, SetMultiTimeFlag] = useState(false);
  const [containerDetails, SetContainerDetails] = useState([]);
  const [truckdetailsList, setTruckdetailsList] = useState([]);
  const [truck, setTruck] = React.useState(1);
  const [render, setRender] = useState(1);
  const [render1, setRender1] = useState(0);
  //const [data, setData] = useState([]);
  const [masterVals, setMasterVals] = useState({
    intervalOpts: [],
    compMouted: false,
  });
  const [requestDetails, setRequestDetails] = useState({
    requestTruckType: [],
    containerList: [],
  });
  const [disableHrs, setDisableHrs] = useState(0);
  

  const handleNext = props.handleNext;
  const containers = props.containers;
  // const containerstemp = containers;

  console.log("requestDetails from booking page", containers);
  const toggleForm = {
    multiLocFlag: false,
    multiTimeFlag: false,
    NoOfTrucks: 1,
  };

  const truckDetails = {
    index: 0,
    truckType: "",
    date_time: "",
    interval: "",
  };

  useEffect(() => {
    
    truckdetailsList.splice(0, truckdetailsList.length)
    const loadAddress = async () => {
      AddressService.fetchAddress()
        .then((response) => {
          console.log("response in select Location", response);
          setAddressData(response.addressDtoList);
          setDisableHrs(response.disableHrs);
        })
        .catch((error) => {
          console.log("error");
        });
    };

    loadAddress();

    setMasterVals((prevstate) => ({
      intervalOpts: [
        { label: "30 min", value: "30 min" },
        { label: "1hr", value: "1hr" },
        { label: "2hr", value: "2hr" },
        { label: "3hr", value: "3hr" },
        { label: "4hr", value: "4hr" },
      ],
      compMouted: true,
    }));

    let obj = {};
    obj = {
      multiLocFlag: false,
      multiTimeFlag: false,
      NoOfTrucks: 1,
    };
    setFormvalues(obj);
    setKey(!key);
    console.log(obj);
    truckdetailsList.push(truckDetails);
    //setTruckdetailsList(truckDetails);
  }, [render1]);

  return formvalues ? (
    <DataForm intialVals={formvalues} {...props} key={key} />
  ) : (
    <></>
  );

  function DataForm(props) {
    const [termsAndCondition, setTermsAndCondition] = useState(false);
    const [showToaster1, setShowToaster1] = useState(null);
    const [showToaster2, setShowToaster2] = useState(null);
    const [showToaster3, setShowToaster3] = useState(null);
    const [data, setData] = useState([]);
    // const [data, setData] = useState([]);
    const methods = useForm({
      // resolver: yupResolver(schema),
      mode: "onChange",
      reValidateMode: "onChange",
      defaultValues: toggleForm,
    });

    const handleChange = (event) => {
      let x = [];

      console.log("before loop", truckdetailsList);
      setTruck(event.target.value);
      setTruckList(Array.from(Array(event.target.value).keys()));
      if (event.target.value > truckdetailsList.length) {
        for (
          let i = 1;
          i <= event.target.value - truckdetailsList.length;
          i++
        ) {
          let truckDetails = {
            index: i,
            truckType: "",
            date_time: "",
            interval: "",
          };
          x.push(truckDetails);
        }
        setTruckdetailsList([...truckdetailsList, ...x]);
      }
      if (event.target.value < truckdetailsList.length) {
        for (let i = 1; i <= event.target.value; i++) {
          let truckDetails = {
            index: truckdetailsList[i].index,
            truckType: truckdetailsList[i].truckType,
            date_time: truckdetailsList[i].date_time,
            interval: truckdetailsList[i].interval,
          };
          x.push(truckDetails);
        }
        setTruckdetailsList(x);
      }

      setRender(render + 1);
    };

    const handleSwitchChange1 = (event) => {
      SetMultiLocFlag(event.target.checked);
      SetMultiTimeFlag(false);
    };

    const handleSwitchChange2 = (event) => {
      SetMultiTimeFlag(event.target.checked);
    };

    const handleCheckBoxChange = (event) => {
      setTermsAndCondition(event.target.checked);
    };

    return (
      <>
        {masterVals.compMouted && (
          <FormProvider {...methods}>
            <Grid container>
              <Grid item xs={12} >
                {" "}
                <FormControlLabel
                 
                  control={
                    <Switch
                      checked={multiLocFlag}
                      onChange={handleSwitchChange1}
                      name="multiLocFlag"
                    />
                  }
                  label={<span style={{ fontSize: '18px',fontWeight:"bold" }}>Would You like to select multiple drop locations?</span>}
                  //label="Would You like to select multiple drop locations?"
                  labelPlacement="start"
                  style={{ fontWeight: "bold",color :"#000000"}}
                />
              </Grid>
              {!multiLocFlag && (
                <Grid item xs={12}>
                  <FormControlLabel
                    control={
                      <Switch
                       
                        checked={multiTimeFlag}
                        onChange={handleSwitchChange2}
                        name="multiTimeFlag"
                      />
                    }
                    label={<span style={{ fontSize: '18px',fontWeight:"bold" }}>Would You like to drop containers at different Date/time?</span>}
                   // label="Would You like to drop containers at different Date/time?"
                    labelPlacement="start"
                  
                  />
                </Grid>
              )}

              {!multiLocFlag && (
                <Grid item xs={12} style={{paddingLeft:"30px"}}>
                  <StyledGrid container spacing={5} >
                  <Grid item xs={2} sm={4} md={3}>
                      <AddAddressCard render={()=>{setRender1(render1+1)}}/>
                    </Grid>
                    {addressData?.map((address, inx) => (
                      <Grid
                        item
                        xs={2}
                        sm={4}
                        md={3}
                        key={addressData.indexOf(address)}
                        
                    
                      >
                        <AddressCard
                          address={address}
                          editFlag="false"
                          deleteFlag="false"
                          saved={(e) => {
                            console.log("rendered....");
                            setData(e);
                          }}
                          divider="Deliver to this Location"
                        />
                      </Grid>
                    ))}
                   
                  </StyledGrid>
                </Grid>
              )}
              {!multiLocFlag && !multiTimeFlag && (
                <>
                  
                  <Grid item xs={12} style={{paddingLeft:"20px"}}>
                    <InputLabel
                      style={{
                        fontSize: "18px",
                        color: "#000000",
                        fontWeight: "bold",
                      }}
                    >
                      Truck Details
                    </InputLabel>
                  </Grid>
                  <Grid container spacing={0} style={{paddingLeft:"20px"}}>
                    <Grid item xs={2} style={{ paddingTop: "10px" }}>
                      <InputLabel style={{ fontSize: "16px", color: "#494949" }}>
                        Number of trucks required
                      </InputLabel>
                    </Grid>
                    <Grid item xs={2}>
                      <FormControl variant="outlined" >
                        <StyledSelectBox
                          value={truck}
                          onChange={handleChange}
                         
                        >
                          <MenuItem value={1}>1</MenuItem>
                          <MenuItem value={2}>2</MenuItem>
                          <MenuItem value={3}>3</MenuItem>
                          <MenuItem value={4}>4</MenuItem>
                          <MenuItem value={5}>5</MenuItem>
                        </StyledSelectBox>
                      </FormControl>
                    </Grid>
                  </Grid>
                  <Grid item xs={12} style={{marginTop:"5px",paddingLeft:"20px"}}>
                    <TruckType
                      truckdetailsList={truckdetailsList}
                      disableHrs={disableHrs}
                    />
                  </Grid>
                  
                </>
              )}
            </Grid>
            <div style={{paddingLeft:"20px"}}>
              <DeliverContainer
                containers={containers}
                multiLocFlag={multiLocFlag}
                multiTimeFlag={multiTimeFlag}
                containerDetails={containerDetails}
                data={data}
                addressData={addressData}
              />
            </div>

            <div style={{paddingLeft:"20px"}}>
              <Paper
                style={{
                  backgroundColor: "#F7F7F7",
                  marginBottom: "10px",
                  marginTop: "20px",
                 
                  height: "68px",
                  border: "1px solid #EDEDED",
                  boxShadow:"0 0 0 0rem"
                }}
              >
                <div>
                  <FormControlLabel
                    style={{ marginLeft: "10px" ,marginTop:"12px",fontSize:"5px" }}
                    control={
                     
                      // <Checkbox
                        
                      //   checked={termsAndCondition}
                      //   onChange={handleCheckBoxChange}
                      //   name="termsAndCondition"
                      // />
                      <Checkbox
    checked={termsAndCondition}
    onChange={handleCheckBoxChange}
    name="termsAndCondition"
    style={{
      transform: "scale(0.7)",
     
  }}
    
 />
                    }
                    label={<span style={{ fontSize: '14px',color:"#9A9A9A",width:"171px",height:"16px"}}>I accept the terms and conditions</span>}
                   //label="I accept the terms and conditions"
                  />

                  <Button
                    style={{ float: "right",fontWeight:"lighter",marginTop:"15px" ,marginRight:"25px",width:"122px",height:"41px",fontSize:"17px"}}
                    variant="contained"
                    color="primary"
                    onClick={() => {
                      var valid1 = null;
                      var valid2 = null;
                      setShowToaster1(null);
                      setShowToaster2(null);
                      setShowToaster3(null);
                      if (multiLocFlag === true || multiTimeFlag === true) {
                        var invalidContainerCount = 0;

                        containers.map((container, inx) => {
                          if (
                            container.consigneeContactName === undefined ||
                            container.consigneeContactNumber === undefined ||
                            container.dropAddress === undefined ||
                            container.refVehicleTypeCode === undefined ||
                            container.date_time === undefined ||
                            container.date_time === "" ||
                            container.date_time === null ||
                            container.date_time == "Invalid Date"

                          )
                            invalidContainerCount = invalidContainerCount + 1;
                        });
                        if (invalidContainerCount > 0) {
                          setShowToaster2(true);

                          valid1 = false;
                        } else {
                          setShowToaster2(false);

                          valid1 = true;
                        }
                      } else if (
                        (multiLocFlag === false && multiTimeFlag === true) ||
                        (multiLocFlag === false && multiTimeFlag === false)
                      ) {
                        var invalidContainerCount = 0;
                        var invalidTruckCount = 0;
                        containers.map((container, inx) => {
                          if (
                            container.consigneeContactName === undefined ||
                            container.consigneeContactNumber === undefined ||
                            container.dropAddress === undefined
                          )
                            invalidContainerCount = invalidContainerCount + 1;
                        });
                        if (invalidContainerCount > 0) {
                          setShowToaster2(true);

                          valid1 = false;
                        } else {
                          setShowToaster2(false);

                          valid1 = true;
                        }

                        truckdetailsList.map((truckDetail, inx) => {
                          if (
                            truckDetail.vehicleCode === undefined ||
                            truckDetail.date_time === "" || truckDetail.date_time===null || truckDetail.date_time == "Invalid Date"
                          )
                            invalidTruckCount = invalidTruckCount + 1;
                        });
                        if (invalidTruckCount > 0) {
                          setShowToaster3(true);

                          valid2 = false;
                        } else {
                          setShowToaster3(false);

                          valid2 = true;
                        }
                      }

                      if (
                        termsAndCondition &&
                        valid1 &&
                        (valid2 || valid2 === null)
                      ) {
                        if (multiLocFlag === true || multiTimeFlag === true) {
                          {
                            requestDetails.containerList = containers;
                            requestDetails.multiLocAndTime = true;
                          }
                          console.log("Button clicked :", requestDetails);
                        } else if (
                          (multiLocFlag === false && multiTimeFlag === true) ||
                          (multiLocFlag === false && multiTimeFlag === false)
                        ) {
                          {
                            let truckList = [];
                            truckdetailsList.map((truckDetail, inx) => {
                              let truck = {
                                index: truckDetail.index,
                                vehicleCode: truckDetail.vehicleCode,
                                vehicleName: truckDetail.vehicleName,
                                containerNumber: null,
                                dateAndTime: truckDetail.date_time,
                                interval: truckDetail.interval,
                              };
                              truckList.push(truck);
                            });

                            requestDetails.requestTruckType = truckList;
                            let containerList = [];
                            containers.map((container, inx) => {
                              let contain = {
                                boeNumber: container.boeNumber,
                                checked: container.checked,
                                consigneeContactEmail:
                                  container.consigneeContactEmail,
                                consigneeContactName:
                                  container.consigneeContactName,
                                container_number: container.container_number,
                                deliveryOrderValidity:
                                  container.deliveryOrderValidity,
                                dpwTransactionId: container.dpwTransactionId,
                                dropAddress: container.dropAddress,
                                holdAuthority: container.holdAuthority,
                                iso_code: container.iso_code,
                                locationCode: container.locationCode,
                                refStatus: container.refStatus,
                                storagePaidTill: container.storagePaidTill,
                                dateAndTime: null,
                                refVehicleTypeCode: null,
                                //contactNumber: container.contactNumber,
                                consigneeContactNumber:
                                  container.consigneeContactNumber,
                                addressCode: container.addressCode,
                                consigneeCode: container.consigneeCode,
                                dropZone: container.dropZone,
                                pickupLocation: container.pickupLocation,
                                phoneNumber: container.phoneNumber,
                                addressLine1: container.addressLine1
                              };
                              containerList.push(contain);
                            });
                            requestDetails.containerList = containerList;
                            requestDetails.multiLocAndTime = false;

                            console.log("Button2 clicked :", requestDetails);
                          }
                        } else {
                          console.log("Click terms");
                        }
                        handleNext(requestDetails);
                      } else if (
                        !termsAndCondition &&
                        valid1 &&
                        (valid2 || valid2 === null)
                      ) {
                        setShowToaster1(true);
                      }
                    }}
                  >
                    Continue
                  </Button>
                  {showToaster1 && (
                    <Toast
                      icon="error"
                      title="Terms And Condition"
                      message="Please indicate that you accept the Terms and Conditions "
                      showToast={() => {
                        setShowToaster1(false);
                      }}
                      position="top-right"
                    />
                  )}
                  {showToaster2 && (
                    <Toast
                      icon="error"
                      title="Data Missing!"
                      message="Please provide the Container data "
                      showToast={() => {
                        setShowToaster2(false);
                      }}
                      position="top-right"
                    />
                  )}
                  {showToaster3 && (
                    <Toast
                      icon="error"
                      title="Data Missing!"
                      message="Please provide the Truck data"
                      showToast={() => {
                        setShowToaster3(false);
                      }}
                      position="top-right"
                    />
                  )}
                </div>
              </Paper>
            </div>
          </FormProvider>
        )}
      </>
    );
  }
}

export default SelectLocations;
