import React, { useEffect, useState } from "react";
import * as Yup from "yup";
import { Grid, makeStyles } from "@material-ui/core";
import { NO_DIALOG } from "../../lib/common/Constants";
import { postHttp, getHttp } from "../../lib/common/HttpService";
import * as endpointContants from "../../utils/ptmsEndpoints";
import MapComponent from "../googlemap/Map";
import TextField from "@material-ui/core/TextField";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import { Formik, Form } from "formik";
import TextInput from "../../lib/components/txtinput/textInput";
import SelectBox from "../../lib/components/select/selectBox";
import { Dialog, DialogTitle, DialogContent, Button } from "@material-ui/core";



const CreateEditAddressPopup = (props) => {
  console.log("props in createEditAdddressPopup::", props);
  const [formvalues, setFormvalues] = useState(null);
  const [key, setKey] = useState(false);
  const [showPopup, setShowPopup] = useState(NO_DIALOG);
  const [pstate, setPstate] = useState(0);
  const [formData, setFormData] = useState("");
  const [address, setAddress] = useState("");
  const [err, setErr] = useState(false);


  const [zoneOpts, setZoneOpts] = useState([]);

  const validationSchema = Yup.object({
    consigneeContactName: Yup.string().required("Consignee Name is Required"),
    addressNickname: Yup.string().required("Address nickname is Required"),
    dropZone: Yup.string().required("Drop Zone is Required"),
    consigneeContactNumber: Yup.string()
      .required("Mobile Number is Required")
      .matches(/^[+][0-9]\d{11,11}$/, "Format must be +971xxxxxxxxx"),
    phoneNumber: Yup.string()
      .required("Phone Number is Required")
      .matches(/^[+][0-9]\d{10,10}$/, "Format must be +971xxxxxxxx"),
      dropAddress:Yup.string().required("Drop Address is Required")
  });

  useEffect(() => {
    const remoteUrl = endpointContants.fetchZone;
    let obj1 = { url: remoteUrl };
    
    getHttp(obj1, true)
      .then((response) => {
        setZoneOpts(response.data.dataItems);
        console.log("-------", response.data.dataItems);
      })
      .then(() => {
        
        setFormvalues(props.createEditFormData);
        setAddress(props.createEditFormData.dropAddress);
      })
      .catch((error) => { });
    
  }, []);

  const onSubmit = (values,f) => {
    console.log("values::::", values);
    console.log("f::::", f);
    if (address !== "") {
      console.log("value ::", values);

      values.dropAddress = address;
      values.code = props.createEditFormData.code;

      console.log("eeeeeeee", values);
      console.log("action :::", props.action);
      let remoteUrl;
      if (props.action === "ADD") remoteUrl = endpointContants.createAddress;
      if (props.action === "EDIT") remoteUrl = endpointContants.updateAddress;
      let obj = {
        url: remoteUrl,
        body: values,
      };
      postHttp(obj, true)
        .then((response) => {
          setPstate(pstate + 1);
          props.onConfirm();
          //setFormData(response);
        })
        .catch((error) => {});
    } else {
      setErr(true);
    }
  };

  const handleClose = (fmk) => {
    props.onClose();
  };

  return (
    <Formik
      initialValues={formvalues ? formvalues : ""}
      validationSchema={validationSchema}
      onSubmit={onSubmit}
      enableReinitialize={true}
    >
      {(formik) => {
        return (
          <>
            { zoneOpts.length!==0 && <Dialog
             
              fullWidth={true}
              maxWidth={"md"}
              open={props.isopen}
            >
              <DialogTitle
                style={{
                  backgroundColor: "#FFF",
                  color: "black",
                  textAlign: "left",
                }}
              >
                <IconButton
                  className="icon-button"
                  aria-label="close"
                  onClick={handleClose}
                >
                  <CloseIcon />
                </IconButton>
                {props.action === "ADD" ? "Add Location" : "Edit Address"}
              </DialogTitle>
              <DialogContent>
                <Form autoComplete="off">
                  <div class="row">
                    <div style={{ width: "300px" }}>
                      <Grid container spacing={1}>
                        <Grid item xs={12}>
                          <TextField
                            // label="Located from map"
                            placeholder={'Locate from map'}
                            name={"dropAddress"}
                            id={"dropAddress"}
                            value={address}
                            error={formik.touched.dropAddress && formik.errors.dropAddress}
                            helperText={formik.touched.dropAddress && formik.errors.dropAddress ? formik.errors.dropAddress : ''}
                            value={address ? address : formik.values?.dropAddress}
                            fullWidth
                          //  error={err}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextInput
                            label="Address line 1"
                            name={"addressLine1"}
                            id={"addressLine1"}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <SelectBox
                            label="Zone"
                            name={"dropZone"}
                            options={zoneOpts}
                           
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextInput
                            label="Nick name"
                            name={"addressNickname"}
                            id={"addressNickname"}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextInput
                            label="Contact Person"
                            name={"consigneeContactName"}
                            id={"consigneeContactName"}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextInput
                            label="Phone Number"
                            placeholder="e.g. +971xxxxxxxx"
                            name={"phoneNumber"}
                            id={"phoneNumber"}
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <TextInput
                            label="Mobile Number"
                            name={"consigneeContactNumber"}
                            id={"consigneeContactNumber"}
                            placeholder="e.g. +971xxxxxxxxx"
                          />
                        </Grid>

                        <Button
                          // style={{
                          //   //backgroundColor: "#C62926",
                          //  // color: "white",
                          //   justifySelf: "right",
                          //   textTransform: "none",
                          //   marginLeft: "250px",
                          //   marginTop: "20px",
                          // }}
                          // color="primary"
                          // type="submit"
                          style={{ float: "right",fontWeight:"lighter",width:"122px",height:"41px",fontSize:"17px",justifySelf: "right",marginLeft: "200px",marginTop: "20px"}}
                          variant="contained"
                          color="primary"
                        >
                          Continue
                        </Button>
                      </Grid>
                    </div>
                    <div className="col">
                      <MapComponent
                        handleClick={(e) => {
                          setAddress(e);
                          formik.setFieldValue('dropAddress', e);
                        }}
                      />
                    </div>
                  </div>
                </Form>
              </DialogContent>
            </Dialog>}
          </>
        );
      }}
    </Formik>
  );
};

export default React.memo(CreateEditAddressPopup);
